import { Route, Switch, HashRouter, Redirect } from "react-router-dom";
import { useHistory } from "react-router";
import "antd/dist/antd.css";
import "./App.css";

function App() {
  const Router = () => {
    const history = useHistory();
    return (
      <>
        <button onClick={() => history.push(`/home`)}>home</button>
        <button onClick={() => history.push(`/search-bus`)}>search</button>
        <Switch>
          <Route path={`/home`} render={() => <div>Home</div>} />
          <Route path={`/search-bus`} render={() => <div>Search Page</div>} />
          <Redirect to={`/home`} />
        </Switch>
      </>
    );
  };
  return (
    <div className="App">
      <HashRouter>
        <Router />
      </HashRouter>
    </div>
  );
}

export default App;
